import React, {useState} from 'react'

// Carregando estilo
import './style.css'

// Utilizando componentes
import { 
    MDBNavbar,
    MDBNavbarBrand,
    MDBNavbarNav,
    MDBNavItem,
    MDBNavLink,
    MDBNavbarToggler,
    MDBCollapse
} from 'mdbreact'


const NavBar = ({links, logo, color}) => {
    
    const [isOpen, setIsOpen] = useState(false)

    const toogleCollapse = () => {
        setIsOpen(!isOpen)
    }

    return (
        <div>
           <MDBNavbar transparent fixed="top" style={{backgroundColor: color}} dark expand="md">
                <MDBNavbarBrand>
                    <MDBNavLink className="logo" to="/">
                        {/* eslint-disable-next-line */}
                        <img className="logoNavbar"src={logo}/>
                    </MDBNavLink>
                </MDBNavbarBrand>
                <MDBNavbarToggler onClick={toogleCollapse} />
                <MDBCollapse id="navbarCollapse3" isOpen={isOpen} navbar>
                    <MDBNavbarNav right>
                        {links.map(link => (
                            <MDBNavItem key={link.path}>
                                {(link.path[0] === '#') ? (
                                    <a className="link" href={link.path}>{link.name}</a>
                                ):(
                                    <MDBNavLink to={link.path}>{link.name}</MDBNavLink>
                                )}
                            </MDBNavItem>
                        ))}
                        
                    </MDBNavbarNav>
                </MDBCollapse>
            </MDBNavbar>
        </div>
    )

}

export default NavBar