import React,{useState} from "react";

import './style.css'

import {  
    MDBContainer, 
    MDBRow, 
    MDBCol, 
    MDBIcon
} from "mdbreact";

import Contact from '../../services/Contact'

const ContactPage = () => {

    const [nome, setNome] = useState('')
    const [email, setEmail] = useState('')
    const [descricao, setDescricao] = useState('')
    const [titulo, setTitulo] = useState('')

    const actioBtnEnviar = () => {
        if(nome.length === 0 || email.length === 0 || descricao.length === 0 || titulo.length === 0){
            alert('Preencha todos os campos!')
        }else{
            Contact.sendContact(email, nome, titulo, descricao).then(result => {
                alert('Proposta entregue, em breve entrarei em contato para negociar')
            }).catch(erro => {
                alert('Verifique sua conexão')
            })
        }
    }

    return (
        <MDBContainer fluid className="rowContato">
            <h2 className="h1-responsive font-weight-bold text-center my-5 titleContact">
                Precisa de um desenvolvedor? Entre em contato!
            </h2>
        <MDBRow >
            <MDBCol md="8" className="md-0 mb-5">
                <MDBRow>
                    <MDBCol md="2"></MDBCol>
                    <MDBCol md="8">
                        <MDBRow>
                            <MDBCol md="6">
                                
                                <input
                                    type="text"
                                    placeholder="Nome"
                                    value={nome}
                                    onChange={event => setNome(event.target.value)}
                                    className="form-control inputContact"
                                />
                                
                            </MDBCol>
                            <MDBCol md="6">
                                <input
                                    type="email"
                                    placeholder="Email"
                                    value={email}
                                    onChange={event => setEmail(event.target.value)}
                                    className="form-control inputContact"
                                />
                            </MDBCol>
                        </MDBRow>
                        <MDBRow>
                        <MDBCol md="12">
                            <input
                                type="text"
                                placeholder="Titulo"
                                value={titulo}
                                onChange={event => setTitulo(event.target.value)}
                                className="form-control inputContact"
                            />
                        </MDBCol>
                        </MDBRow>
                        <MDBRow>
                        <MDBCol md="12">
                            <textarea
                                placeholder="Fale sobre o que você precisa"
                                id="defaultFormLoginEmailEx"
                                className="form-control inputContact"
                                value={descricao}
                                onChange={event => setDescricao(event.target.value)}
                            />
                        </MDBCol>
                        </MDBRow>

                        <MDBRow>
                            <MDBCol md="4"></MDBCol>
                            <MDBCol md="4">
                                <div className="text-center text-md-left">
                                    <center>
                                        <button onClick={actioBtnEnviar} className="btnContact">Enviar</button>
                                    </center>
                                    
                                </div>
                            </MDBCol>
                            <MDBCol md="4"></MDBCol>
                        </MDBRow>
                    </MDBCol>
                    <MDBCol md="2"></MDBCol>
                </MDBRow>
            </MDBCol>

            <MDBCol md="2" className="text-center">
            <ul className="list-unstyled mb-0">
                <li>
                <MDBIcon icon="phone" size="2x" className="white-text mt-4" />
                <p>(88) 99771-3247</p>
                </li>
                <li>
                <MDBIcon icon="envelope" size="2x" className="white-text mt-4" />
                <p style={{marginLeft: -25,}}>daniel.dantas.developer@gmail.com</p>
                </li>
            </ul>
            </MDBCol>
            <MDBCol md="2"></MDBCol>
        </MDBRow>
        </MDBContainer>
    )
}

export default ContactPage;