import React from "react";

// Importando estilo para o carrousel
import './style.css'

// Importando componentes do MDBootstrap
import {
  MDBCarousel,
  MDBCarouselCaption, 
  MDBCarouselInner, 
  MDBCarouselItem, 
  MDBView, 
  MDBMask 
} from "mdbreact";

// Itens é um array com todos os itens para ser inseridos no carrousel

const CarrouselPage = ({itens}) => {
  return (
      
      <MDBCarousel
        activeItem={1}
        length={itens.length}
        showControls={true}
        showIndicators={true}
        className="z-depth-1 carrousel"
      >
      <MDBCarouselInner>
        {itens.map(item => (
          <MDBCarouselItem key={item.id} itemId={item.id}>
            <MDBView>
              <img
                className="d-block w-100 carrousel"
                src={item.image}
                alt="First slide"
              />
            <MDBMask overlay="black-light" />
            </MDBView>
            <MDBCarouselCaption>
              <h3 className="h3-responsive">{item.title}</h3>
              <p>{item.description}</p>
            </MDBCarouselCaption>
          </MDBCarouselItem>
        ))}
      </MDBCarouselInner>
    </MDBCarousel>
    
      
    
  );
}

export default CarrouselPage;