import React from 'react'

import {
    BrowserRouter,
    Route,
    Switch
}from 'react-router-dom'

// Paginas
import Home from './pages/Home'
import NotPage from './pages/NotPage'

export default () =>(
    <BrowserRouter>
        <Switch>
            <Route exact path="/" component={() => <Home />}/>
            <Route exact path="/notpage" component={() => <NotPage />} />
        </Switch>
    </BrowserRouter>
)

