import React from 'react';

// Importando estilo da footer
import './style.css'

// Importando MDBootstrap
import { 
    MDBCol, 
    MDBContainer, 
    MDBRow, 
    MDBFooter 
} from "mdbreact";

export default function Footer(props) {
    return (
        <MDBFooter style={{backgroundColor: props.color}} className="font-small pt-4 mt-4">
          <MDBContainer fluid className="text-center text-md-left">
            <MDBRow>
              <MDBCol md="6">
                <MDBRow>
                  <MDBCol md="3"></MDBCol>
                  <MDBCol md="6">
                  <h5 className="title">Navegação</h5>
                    <ul className="ulLinks">
                        {(props.links.map(link => (
                            <li key={link.path} className="list-unstyled">
                                <a href={link.path}>{link.name}</a>
                            </li>
                        )))}
                    </ul>
                    
                  </MDBCol>
                  <MDBCol md="3"></MDBCol>
                </MDBRow>
              </MDBCol>
              <MDBCol md="6">
                <MDBRow>
                  <MDBCol md='3'></MDBCol>
                  <MDBCol md='6'>
                  {props.children}
                  </MDBCol>
                  <MDBCol md='3'></MDBCol>
                </MDBRow>
              </MDBCol>
            </MDBRow>
          </MDBContainer>
          <div className="footer-copyright text-center py-3">
            <MDBContainer fluid>
                &copy; {new Date().getFullYear()} Copyright: <a href="/">{props.copyText}</a>
            </MDBContainer>
          </div>
        </MDBFooter>
      )
}
